import { useEffect } from 'react';
import { useState } from 'react';
import TinderCard from 'react-tinder-card';
import database from './firebase';
import "./TinderCards.css";
const TinderCards = () => {

    const [people,setPeople] = useState([
        // {
        //     name:"mark",
        //     url:"https://i.guim.co.uk/img/static/sys-images/Guardian/Pix/pictures/2014/11/8/1415445010984/Mark-Zuckerberg-012.jpg?width=300&quality=45&auto=format&fit=max&dpr=2&s=9880c57e0aa7f4f25e613e69be3b5224"
        // },
        // {
        //     name:"robert",
        //     url:"data:image/jpeg;base64,"
        // }
    ]);


    useEffect(()=>{
     const unsubscribe =  database.collection('people').onSnapshot((snapshot)=> (
           setPeople(snapshot.docs.map(doc => doc.data()))
           ));

           return ()=> {
               unsubscribe();
           }
    },[]);
    return ( 
        <div className="card">
            
            {people.map((person)=>(
                <div className="tinderCard__cardContainter">
                    <TinderCard
                        className="swipe"
                        key={person.name}
                        preventSwipe = {"up","down"}
                        >
                        <div
                            style={{backgroundImage:`url(${person.url})`}}
                            className="card"
                        >
                            <h3>{person.name}</h3>
                        </div>
                    </TinderCard>
                </div>
                
            ))}
        </div>
     );
}
 
export default TinderCards;
