import Avatar from '@material-ui/core/Avatar';
import { Link } from 'react-router-dom';
import "./chat.css";

const Chat = ({name,message,image,timestamp}) => {
    return ( 
        <Link to ={`/chats/${name}`}>
           <div className="chat">
            <div className="image">
                <Avatar src={image} />
            </div>
            <div className="chat__details">
                <h2>
                    {name}
                </h2>
                <p>
                    {message}
                </p>
            </div>
            <div className="chat__timeStamp">
                <p>
                  {timestamp}
                </p>
            </div>
        </div>
        </Link>
        
     );
}
 
export default Chat;
