import { Avatar } from "@material-ui/core";
import { useState } from "react";
import "./chatScreen.css";

const ChatScreen = () => {


    const [input,setInput] = useState("");
    const [messages,setMessages] = useState([
        {
            name:"afnan",
            message:"Hey man!",
            image:"https://pbs.twimg.com/profile_images/1132217502635843585/1wbCaSXd_400x400.jpg"
        },
        {
            
            message:"Kdhr hai bharway",
            image:"https://pbs.twimg.com/profile_images/1132217502635843585/1wbCaSXd_400x400.jpg"
        },
        {
            name:"afnan",
            message:"Bas aara hun jani 2 min",
            image:"https://pbs.twimg.com/profile_images/1132217502635843585/1wbCaSXd_400x400.jpg"
        },
        {
            message:"Jaldi aa bhai",
            image:"https://pbs.twimg.com/profile_images/1132217502635843585/1wbCaSXd_400x400.jpg"
        }
    ])

    function handleClick(e){
        setMessages([...messages,{message:input}]);
        setInput("");
        e.preventDefault();
    };

    return ( 
        <div className="chatScreen">
            <p>You matched with afnan at 8/8/2021</p>
            {messages.map((message)=>(
                message.name ? (
                    <div className="chatScreen__message">
                    <Avatar className="chatScreen__image" src={message.image} />
                    <p className="chatScreen__text">{message.message}</p>
                    </div>
                )
                :(
                    <div className="chatScreen__message">
                        <p className="chatScreen__textUser">
                            {message.message}                            
                        </p>
                    </div>
                )

            ))}

            <form className="chatScreen__input" >
                <input
                className="chatScreen__inputFeild"
                 type="text"
                 value={input}
                 placeholder="Type a message.."
                 onChange={(e)=>(setInput(e.target.value))}
                 />
                <button
                className="chatScreen__inputButton"
                type="submit"
                onClick={handleClick}
                >Send</button>
            </form>
        </div>
     );
}
 
export default ChatScreen;
