import Fab from '@material-ui/core/Fab';
import ReplayIcon from '@material-ui/icons/Replay';
import CloseIcon from '@material-ui/icons/Close';
import StarIcon from '@material-ui/icons/Star';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FlashOnIcon from '@material-ui/icons/FlashOn';
import "./swipeButtons.css"

const SwipeButtons = () => {
    return ( 
        <div className="swipe-buttons">
            <Fab color="default" aria-label="replay" className="swipe__repeat">
               <ReplayIcon />
            </Fab>
            <Fab color="default" aria-label="close" className="swipe__close">
               <CloseIcon />
            </Fab>
            <Fab color="default" aria-label="like" className="swipe__star">
               <StarIcon />
            </Fab>
            <Fab color="default" aria-label="fav" className="swipe__fav">
               <FavoriteIcon />
            </Fab>
            <Fab color="default" aria-label="flash" className="swipe__flash">
               <FlashOnIcon />
            </Fab>
        </div>
     );
}
 
export default SwipeButtons;